

.container{
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    width: 80%;
}

.sectionContainer{
    display: flex;
    margin: auto;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 50vw;
    background-color: azure;
    border:2px solid black;
    padding: 4em;
    border-radius: 2em;
}

.words{
    background-color: rgb(211, 153, 132);
    width: 15vw;
    border-radius: 2em;
    margin-right: 2em;
}

.meaning{
    background-color: rgb(119, 172, 95);
    width: 15vw;
    border-radius: 2em;
    margin-left: 4em;
}

.buttonContainer button{
    padding: 10px;
    margin: 15px;
}

.trialOnes{
    background-color: bisque;
    margin-top: 2em;
    border-radius: 2em;
}

.triedElement{
    font-size: 20px;
    font-weight: 700;
}

.wordMeaningBlock{
    display: flex;
    flex-direction: row;
    margin: auto;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wordResult{
    background-color: azure;
    padding: 10px;
}

.meaningResult{
    background-color: azure;
    padding:10px;
}